// src/pages/HomePage.jsx
/**
 * Huvudsidan fr Scrum Board 
 *
 * Styr hela appen:
 * - Firebase-kommunikation via hooks
 * - Uppgifts- och medlemshantering
 * - Navigation mellan Board, Uppgifter och Team
 * - Notifikationer fr anvndarfeedback
 */
import React, { useState, useEffect, useCallback } from "react";
import { useNavigate } from "react-router";

// UI-komponenter
import ScrumBoard from "../components/Board/ScrumBoard.jsx";
import TaskList from "../components/Tasks/TaskList.jsx";
import MemberList from "../components/Members/MemberList.jsx";
import Notification from "../components/UI/Notification.jsx";
import DatabaseSeeder from "../components/Admin/DatabaseSeeder.jsx";
import Button from "../components/UI/Button.jsx";
import { LogoutIcon, UserIcon } from "../components/UI/Icons.jsx";

// Firebase-hooks
import useMembers from "../hooks/useMembers.jsx"; // Teamhantering
import useOptimizedTasks from "../hooks/useOptimizedTasks.jsx"; // Uppgiftshantering
import { useAuth } from "../hooks/useAuth.jsx"; // Autentisering

// Styling fr att gra allt snyggt
import "../styles/pages/HomePage.css";
import "../styles/components/DatabaseSeeder.css";

const HomePage = () => {
  // Hller koll p vilken flik som r aktiv och vad som hnder
  const [activeTab, setActiveTab] = useState("board"); // Vilken vy ser vi just nu?
  const [notification, setNotification] = useState(null); // Meddelanden till anvndaren

  // Mina autentiseringsverktyg - vem r inloggad och hur loggar vi ut?
  const { currentUser, logout, deleteCurrentUser } = useAuth();
  const navigate = useNavigate();

  /**
   * Mina tre favoritomrden inom webbutveckling 
   *
   * Som en utvecklare ser jag webbutveckling i tre delar:
   * - Frontend: Det vackra som anvndarna ser och klickar p
   * - Backend: Den smarta logiken som gr att allt fungerar
   * - UX: Konsten att gra allt intuitivt och anvndarvnligt
   */
  const myTeamSkillAreas = ["frontend", "backend", "ux"];

  /**
   * Hook fr teamhantering
   *
   * Hanterar all data och funktionalitet relaterad till teammedlemmar:
   * - Medlemslista
   * - Laddningsstatus
   * - Felhantering
   * - CRUD-operationer fr medlemmar
   */
  const {
    members,
    loading: membersLoading,
    error: membersError,
    addMember,
    deleteMember,
  } = useMembers();

  /**
   * Optimerad uppgiftshantering
   *
   * Frbttringar: memoization, query-optimering, caching.
   */
  const {
    tasks,
    loading: tasksLoading,
    error: tasksError,
    addTask,
    deleteTask,
    updateTaskStatus,
    assignTask,
  } = useOptimizedTasks({
    realtime: true, // Realtidsuppdateringar
    limit: 100, // Begrnsa fr prestanda
  });

  /**
   * Min personliga notifikationsassistent
   *
   * Som en vnlig kollega som tipsar om vad som hnder i appen.
   * Dyker upp, sger sitt, och frsvinner diskret efter 5 sekunder.
   *
   * @param {string} message - Vad som ska sgas till anvndaren
   * @param {string} type - Knslan i meddelandet (info, success, error)
   */
  const tellUserWhatHappened = (message, type = "info") => {
    // Visa meddelandet fr anvndaren
    setNotification({ message, type });

    // Frsvinn automatiskt s anvndaren slipper stnga manuellt
    setTimeout(() => {
      setNotification(null);
    }, 5000);
  };

  /**
   * Hanterar tillgg av teammedlem
   *
   * Lgger till en ny medlem och visar feedback om resultatet.
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   *
   * @param {Object} memberData - Medlemsdata frn formulret
   */
  const welcomeNewTeammate = useCallback(
    async (memberData) => {
      // Lgg till den nya personen i teamet
      const result = await addMember(memberData);

      // Bertta fr anvndaren hur det gick
      if (result.success) {
        tellUserWhatHappened(
          `Vlkommen ${memberData.name} till teamet! `,
          "success"
        );
      } else {
        tellUserWhatHappened(
          `Kunde inte lgga till ${memberData.name}: ${result.error}`,
          "error"
        );
      }
    },
    [addMember]
  );

  /**
   * Hanterar borttagning av teammedlem
   *
   * Tar bort en medlem och visar feedback om resultatet.
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   *
   * @param {string} memberId - ID fr medlemmen som ska tas bort
   */
  const sayGoodbyeToTeammate = useCallback(
    async (memberId) => {
      // Ta bort personen frn teamet
      const result = await deleteMember(memberId);

      // Meddela anvndaren vad som hnde
      if (result.success) {
        tellUserWhatHappened("Teammedlem har lmnat projektet.", "success");
      } else {
        tellUserWhatHappened(
          `Kunde inte ta bort teammedlem: ${result.error}`,
          "error"
        );
      }
    },
    [deleteMember]
  );

  /**
   * Hanterar tillgg av uppgift
   *
   * Lgger till en ny uppgift och visar feedback om resultatet.
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   *
   * @param {Object} taskData - Uppgiftsdata frn formulret
   */
  const createNewWorkItem = useCallback(
    async (taskData) => {
      // Skapa en ny uppgift i systemet
      const result = await addTask(taskData);

      // Bertta fr anvndaren vad som hnde
      if (result.success) {
        tellUserWhatHappened(
          `Ny uppgift "${taskData.title}" r redo att tacklas! `,
          "success"
        );
      } else {
        tellUserWhatHappened(
          `Kunde inte skapa uppgift: ${result.error}`,
          "error"
        );
      }
    },
    [addTask]
  );

  /**
   * Hanterar statusndring fr uppgift
   *
   * Uppdaterar en uppgifts status och visar feedback om resultatet.
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   *
   * @param {string} taskId - Uppgiftens ID
   * @param {string} newStatus - Ny status (Nytt/Pgende/Klar)
   */
  const moveTaskToNewColumn = useCallback(
    async (taskId, newStatus) => {
      // Flytta uppgiften till rtt kolumn p tavlan
      const result = await updateTaskStatus(taskId, newStatus);

      // Lt anvndaren veta vad som hnde
      if (result.success) {
        tellUserWhatHappened(
          `Uppgift flyttad till "${newStatus}" - bra jobbat! `,
          "success"
        );
      } else {
        tellUserWhatHappened(
          `Kunde inte flytta uppgift: ${result.error}`,
          "error"
        );
      }
    },
    [updateTaskStatus]
  );

  /**
   * Hanterar tilldelning av uppgift
   *
   * Tilldelar en uppgift till en teammedlem eller tar bort tilldelning.
   * Visar feedback om resultatet.
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   *
   * @param {string} taskId - Uppgiftens ID
   * @param {string|null} memberId - Medlemmens ID eller null
   * @param {string|null} memberName - Medlemmens namn fr visning
   */
  const assignWorkToTeammate = useCallback(
    async (taskId, memberId, memberName) => {
      // Ge uppgiften till rtt person i teamet
      const result = await assignTask(taskId, memberId, memberName);

      // Bertta vad som hnde
      if (result.success) {
        if (memberId) {
          tellUserWhatHappened(
            `${memberName} tar nu hand om uppgiften! `,
            "success"
          );
        } else {
          tellUserWhatHappened(
            "Uppgiften r nu ledig fr vem som helst.",
            "info"
          );
        }
      } else {
        tellUserWhatHappened(
          `Kunde inte tilldela uppgift: ${result.error}`,
          "error"
        );
      }
    },
    [assignTask]
  );

  /**
   * Hanterar borttagning av uppgift
   *
   * Tar bort en uppgift permanent frn databasen och
   * visar feedback om resultatet.
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   *
   * @param {string} taskId - Uppgiftens ID
   */
  const removeTaskCompletely = useCallback(
    async (taskId) => {
      // Ta bort uppgiften helt frn systemet
      const result = await deleteTask(taskId);

      // Meddela anvndaren resultatet
      if (result.success) {
        tellUserWhatHappened(
          "Uppgift borttagen - som om den aldrig funnits! ",
          "success"
        );
      } else {
        tellUserWhatHappened(
          `Kunde inte ta bort uppgift: ${result.error}`,
          "error"
        );
      }
    },
    [deleteTask]
  );

  /**
   * Hanterar utloggning
   *
   * Stdar upp efter anonyma anvndare och skickar alla tillbaka till login.
   */
  const handleLogout = async () => {
    try {
      // Fr anonyma anvndare: ta bort dem frn Firebase frst
      if (currentUser?.isAnonymous) {
        console.log("Stdar upp anonym anvndare:", currentUser.uid);

        try {
          const deleteResult = await deleteCurrentUser();
          if (deleteResult.success) {
            console.log("Anvndare borttagen! ");
            navigate("/login");
            return;
          } else {
            // Om borttagning misslyckades, fortstt med vanlig utloggning
            console.warn("Kunde inte ta bort anvndaren:", deleteResult.error);
          }
        } catch (error) {
          console.error("Fel vid stdning:", error);
        }
      }

      // Vanlig utloggning
      const result = await logout();
      if (result.success) {
        navigate("/login");
      } else {
        tellUserWhatHappened("Kunde inte logga ut. Frsk igen.", "error");
      }
    } catch (error) {
      tellUserWhatHappened("Ett fel uppstod vid utloggning.", "error");
    }
  };

  /**
   * Visar felmeddelanden vid datahmtningsfel
   *
   * vervakar felstatus och visar notifikationer nr
   * fel uppstr vid hmtning av data.
   */
  useEffect(() => {
    // Bertta fr anvndaren om ngot gick fel med teamdata
    if (membersError) {
      tellUserWhatHappened(
        `Problem med att hmta teammedlemmar: ${membersError}`,
        "error"
      );
    }

    // Bertta fr anvndaren om ngot gick fel med uppgiftsdata
    if (tasksError) {
      tellUserWhatHappened(
        `Problem med att hmta uppgifter: ${tasksError}`,
        "error"
      );
    }
  }, [membersError, tasksError]); // Krs nr fel uppstr

  /**
   * Renderar applikationens huvudgrnssnitt
   *
   * Bygger upp UI med header, navigation, innehll och notifikationer
   */
  return (
    <div className="home-page">
      {/* Applikationens header med titel och anvndarinfo */}
      <header className="app-header">
        <h1>Scrum Board</h1>

        {/* Anvndarinfo och utloggningsknapp */}
        <div className="user-controls">
          {currentUser?.isAnonymous ? (
            <div className="user-profile">
              <div className="user-avatar">
                <UserIcon size={18} color="#3b82f6" />
              </div>
              <div className="user-info">
                <span className="user-name">Anonym anvndare</span>
                <span className="user-status">Temporr session</span>
              </div>
              <Button
                onClick={handleLogout}
                variant="outline"
                size="small"
                className="logout-button"
                iconRight={<LogoutIcon size={16} />}
                aria-label="Logga ut"
              >
                Avsluta
              </Button>
            </div>
          ) : (
            <div className="user-profile">
              <div className="user-avatar">
                <UserIcon size={18} color="#3b82f6" />
              </div>
              <div className="user-info">
                <span className="user-name">{currentUser?.email}</span>
                <span className="user-status">Inloggad</span>
              </div>
              <Button
                onClick={handleLogout}
                variant="outline"
                size="small"
                className="logout-button"
                iconRight={<LogoutIcon size={16} />}
                aria-label="Logga ut"
              >
                Logga ut
              </Button>
            </div>
          )}
        </div>
      </header>

      {/* Fliknavigation fr att vxla mellan olika vyer */}
      <nav className="app-tabs" aria-label="Application Navigation">
        {/* Board-fliken (kanban-vy) */}
        <button
          className={`tab-button ${activeTab === "board" ? "active" : ""}`}
          onClick={() => setActiveTab("board")}
          aria-selected={activeTab === "board"}
          role="tab"
        >
          Board
        </button>

        {/* Uppgifter-fliken (listvy med filter och sortering) */}
        <button
          className={`tab-button ${activeTab === "tasks" ? "active" : ""}`}
          onClick={() => setActiveTab("tasks")}
          aria-selected={activeTab === "tasks"}
          role="tab"
        >
          Uppgifter
        </button>

        {/* Team-fliken (hantering av teammedlemmar) */}
        <button
          className={`tab-button ${activeTab === "members" ? "active" : ""}`}
          onClick={() => setActiveTab("members")}
          aria-selected={activeTab === "members"}
          role="tab"
        >
          Team
        </button>
      </nav>

      {/* Huvudinnehllsomrde som visar olika komponenter baserat p aktiv flik */}
      <main className="app-content" role="tabpanel" aria-live="polite">
        {/* Scrum Board-vyn (kanban-vy med kolumner) */}
        {activeTab === "board" && (
          <ScrumBoard
            tasks={tasks}
            members={members}
            onAddTask={createNewWorkItem}
            onUpdateTaskStatus={moveTaskToNewColumn}
            onAssignTask={assignWorkToTeammate}
            onDeleteTask={removeTaskCompletely}
            categories={myTeamSkillAreas}
            isLoading={tasksLoading || membersLoading}
          />
        )}

        {/* Uppgiftslista med filter och sortering */}
        {activeTab === "tasks" && (
          <TaskList
            tasks={tasks}
            members={members}
            onAddTask={createNewWorkItem}
            onUpdateTaskStatus={moveTaskToNewColumn}
            onAssignTask={assignWorkToTeammate}
            onDeleteTask={removeTaskCompletely}
            categories={myTeamSkillAreas}
            isLoading={tasksLoading || membersLoading}
          />
        )}

        {/* Teammedlemslista */}
        {activeTab === "members" && (
          <MemberList
            members={members}
            onAddMember={welcomeNewTeammate}
            onDeleteMember={sayGoodbyeToTeammate}
            roles={myTeamSkillAreas}
            isLoading={membersLoading}
          />
        )}
      </main>

      {/* Notifikationssystem som visar feedback till anvndaren */}
      {notification && (
        <Notification
          message={notification.message}
          type={notification.type}
          onClose={() => setNotification(null)}
        />
      )}

      {/* Verktyg fr att fylla databasen med exempeldata (alltid synligt fr felskning) */}
      <DatabaseSeeder onSuccess={tellUserWhatHappened} />
    </div>
  );
};

export default HomePage;
