// src/components/UI/Modal.jsx
/**
 * teranvndbar modalkomponent
 *
 * Flexibel dialog med React Portal.
 * Stder olika storlekar, Escape-stngning,
 * frhindrar bakgrundsscrollning.
 */
import React, { useEffect } from "react";
import ReactDOM from "react-dom";
import "../../styles/components/Modal.css";
import Button from "./Button.jsx";

const Modal = ({
  isOpen,
  onClose,
  title,
  children,
  footer,
  size = "medium",
}) => {
  /**
   * Hanterar Escape-tangent och scrollning
   */
  useEffect(() => {
    // Stng med Escape
    const handleEscape = (e) => {
      if (e.key === "Escape" && isOpen) {
        onClose();
      }
    };

    // Registrera event-lyssnare
    window.addEventListener("keydown", handleEscape);

    // Frhindra bakgrundsscrollning
    if (isOpen) {
      document.body.style.overflow = "hidden";
    }

    // Stda upp vid avmontering
    return () => {
      window.removeEventListener("keydown", handleEscape);
      document.body.style.overflow = "auto";
    };
  }, [isOpen, onClose]);

  if (!isOpen) return null;

  /**
   * Frhindrar bubbling av klickhndelser
   */
  const handleModalClick = (e) => {
    e.stopPropagation();
  };

  // React Portal fr korrekt rendering
  return ReactDOM.createPortal(
    <div className="modal-overlay" onClick={onClose}>
      {/* Modal-container */}
      <div className={`modal modal-${size}`} onClick={handleModalClick}>
        {/* Header */}
        <div className="modal-header">
          <h2 className="modal-title">{title}</h2>
          <button className="modal-close" onClick={onClose} aria-label="Stng">
            
          </button>
        </div>

        {/* Innehll */}
        <div className="modal-content">{children}</div>

        {/* Footer */}
        {footer && <div className="modal-footer">{footer}</div>}
      </div>
    </div>,
    document.body // Renderar i body fr korrekt z-index
  );
};

export default Modal;
