// src/components/Members/MemberList.jsx
/**
 * Teamversikt med medlemshantering
 *
 * Visar medlemmar med profilbild, namn och roll.
 * Lgg till och ta bort medlemmar med bekrftelse.
 */
import React, { useState } from "react";
import Button from "../UI/Button.jsx";
import Modal from "../UI/Modal.jsx";
import AddMemberForm from "./AddMemberForm.jsx";
import "../../styles/components/MemberList.css";

const MemberList = ({
  members,
  onAddMember,
  onDeleteMember,
  roles = ["frontend", "backend", "ux"],
  isLoading = false,
}) => {
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [memberToDelete, setMemberToDelete] = useState(null);

  /**
   * Lgger till ny medlem och stnger modal
   */
  const handleAddMember = (memberData) => {
    onAddMember(memberData);
    setIsAddModalOpen(false);
  };

  /**
   * Tar bort medlem efter bekrftelse
   */
  const handleConfirmDelete = () => {
    if (memberToDelete) {
      onDeleteMember(memberToDelete.id);
      setMemberToDelete(null);
    }
  };

  /**
   * Returnerar CSS-klass baserat p medlemsroll
   *
   * Ger visuell frgkodning fr olika roller:
   * - Frontend: bl
   * - Backend: grn
   * - UX: lila
   *
   * @param {string} role - Medlemmens roll
   * @returns {string} CSS-klassnamn fr frgkodning
   */
  const getRoleColorClass = (role) => {
    switch (role.toLowerCase()) {
      case "frontend":
        return "role-frontend";
      case "backend":
        return "role-backend";
      case "ux":
        return "role-ux";
      default:
        return "role-default";
    }
  };

  return (
    <div className="member-list-container">
      <div className="member-list-header">
        <h2>Teammedlemmar</h2>
        <Button onClick={() => setIsAddModalOpen(true)} variant="primary">
          Lgg till medlem
        </Button>
      </div>

      {isLoading ? (
        <div className="loading-indicator">
          <span>Laddar teammedlemmar</span>
        </div>
      ) : members.length === 0 ? (
        <div className="empty-state">
          <p>Inga teammedlemmar tillagda nnu.</p>
          <Button onClick={() => setIsAddModalOpen(true)} variant="secondary">
            Lgg till din frsta teammedlem
          </Button>
        </div>
      ) : (
        <div className="member-grid">
          {members.map((member) => (
            <div key={member.id} className="member-card">
              <div className="member-avatar">
                {member.avatar ? (
                  <img src={member.avatar} alt={member.name} />
                ) : (
                  <div className="member-initials">
                    {member.name
                      .split(" ")
                      .map((n) => n[0])
                      .join("")}
                  </div>
                )}
              </div>
              <div className="member-info">
                <h3 className="member-name">{member.name}</h3>
                <span
                  className={`member-role ${getRoleColorClass(member.role)}`}
                >
                  {member.role.charAt(0).toUpperCase() + member.role.slice(1)}
                </span>
              </div>
              <Button
                onClick={() => setMemberToDelete(member)}
                variant="danger"
                size="small"
              >
                Ta bort
              </Button>
            </div>
          ))}
        </div>
      )}

      {/* Modal fr att lgga till nya teammedlemmar */}
      <Modal
        isOpen={isAddModalOpen}
        onClose={() => setIsAddModalOpen(false)}
        title="Lgg till teammedlem"
      >
        <AddMemberForm onAddMember={handleAddMember} roles={roles} />
      </Modal>

      {/* Bekrftelsemodal fr borttagning av teammedlem */}
      <Modal
        isOpen={!!memberToDelete}
        onClose={() => setMemberToDelete(null)}
        title="Bekrfta borttagning"
        footer={
          <>
            <Button onClick={() => setMemberToDelete(null)} variant="secondary">
              Avbryt
            </Button>
            <Button onClick={handleConfirmDelete} variant="danger">
              Ta bort
            </Button>
          </>
        }
      >
        {memberToDelete && (
          <p>
            Vill du verkligen ta bort {memberToDelete.name} frn teamet? Det gr
            inte att ngra, och alla uppgifter som personen jobbar med just nu
            blir otilldelade.
          </p>
        )}
      </Modal>
    </div>
  );
};

export default MemberList;
