// src/utils/sortUtils.jsx

/**
 * Sorteringsfunktioner fr uppgifter 
 *
 * Olika stt att ordna uppgifter.
 * Alla funktioner skapar nya listor utan att ndra originalet.
 */

/**
 * Sorterar nyast frst 
 *
 * @param {Array} tasks - Uppgifter att sortera
 * @returns {Array} Sorterad lista
 */
export const sortByNewest = (tasks) => {
  // Kopia och sortering
  return [...tasks].sort((a, b) => b.creationTimestamp - a.creationTimestamp);
};

/**
 * ldst frst! 
 *
 * "First in, first out" - som en k p banken. Perfekt nr du
 * vill beta av uppgifter i den ordning de kom in. Rttvist!
 *
 * @param {Array} tasks - Uppgifterna du vill sortera
 * @returns {Array} En ny lista med de ldsta frst
 */
export const sortByOldest = (tasks) => {
  // Gr en kopia frst (skert r skert)
  // Sen sorterar s det ldsta kommer frst
  return [...tasks].sort((a, b) => a.creationTimestamp - b.creationTimestamp);
};

/**
 * A till  
 *
 * Som en telefonbok eller bibliotekskatalog! Perfekt nr du
 * letar efter ngot och vet ungefr vad det heter. Funkar
 * fint med vra svenska , ,  ocks.
 *
 * @param {Array} tasks - Uppgifterna du vill sortera
 * @returns {Array} En ny lista i alfabetisk ordning
 */
export const sortByTitleAsc = (tasks) => {
  // Gr en kopia frst (som alltid)
  // Sen sorterar alfabetiskt (localeCompare fixar , , )
  return [...tasks].sort((a, b) => a.title.localeCompare(b.title));
};

/**
 *  till A (baklnges!) 
 *
 * Ibland r det kul att vnda p steken! Kanske ser man
 * mnster man annars missar? Eller bara fr att det r kul.
 *
 * @param {Array} tasks - Uppgifterna du vill sortera
 * @returns {Array} En ny lista i omvnd bokstavsordning
 */
export const sortByTitleDesc = (tasks) => {
  // Gr en kopia frst (som en god vana)
  // Sen sorterar baklnges, frn  till A
  return [...tasks].sort((a, b) => b.title.localeCompare(a.title));
};
