// src/hooks/useMembers.jsx
import { useState, useEffect } from "react";
import { db } from "../firebase/firebaseConfig.jsx";

// SMART IMPORT: Bara det vi behver frn Firebase (mindre paket = snabbare app)
import { ref, onValue, push, remove } from "firebase/database";

/**
 * Min teamchef-assistent! 
 *
 * Som en HR-avdelning fr min app - hller koll p alla i teamet.
 * Lgger till nya kollegor, sger hej d till de som slutar,
 * och hller listan uppdaterad i realtid s alla ser samma sak.
 *
 * @returns {Object} Allt du behver fr att hantera teamet
 */
const useMembers = () => {
  // Mina tre viktiga tillstnd fr teamhantering
  const [members, setMembers] = useState([]); // Alla hjltar i teamet
  const [loading, setLoading] = useState(true); // Hmtar vi data just nu?
  const [error, setError] = useState(null); // Har ngot gtt snett?

  // Adressen i Firebase dr alla teammedlemmar bor
  const MEMBERS_REF_PATH = "members";

  /**
   * Min realtidslyssnare - som att ha rat mot vggen! 
   *
   * Stter upp en direktlinje till Firebase som berttar direkt
   * nr ngon ny kommer till teamet eller nr ngon lmnar.
   * Ingen behver uppdatera sidan - allt hnder automatiskt!
   */
  useEffect(() => {
    const membersRef = ref(db, MEMBERS_REF_PATH);

    try {
      // Startar min "teamspaning" - lyssnar p alla frndringar
      const unsubscribe = onValue(
        membersRef,
        (snapshot) => {
          const data = snapshot.val();
          if (data) {
            // Frvandlar Firebase-data till en snygg array med alla teammedlemmar
            const membersArray = Object.entries(data).map(([id, member]) => ({
              id,
              ...member,
            }));
            setMembers(membersArray);
          } else {
            // Tomt team? Inga problem, vi brjar med en tom lista
            setMembers([]);
          }
          setLoading(false);
        },
        (error) => {
          console.error("useMembers: Error fetching members:", error);
          setError(error.message);
          setLoading(false);
        }
      );

      // Stdar upp nr komponenten frsvinner (som att stnga av radion)
      return () => {
        unsubscribe();
      };
    } catch (error) {
      console.error("useMembers: Exception in effect:", error);
      setError(error.message);
      setLoading(false);
    }
  }, []);

  /**
   * Vlkomnar en ny teammedlem! 
   *
   * Som att skriva in ngon i gstboken - sparar all info
   * om den nya personen s alla kan se vem som r med i teamet.
   *
   * @param {Object} memberData - Allt om den nya personen (namn, roll, avatar)
   * @returns {Object} Berttar om det gick bra eller inte
   */
  const addMember = async (memberData) => {
    try {
      const membersRef = ref(db, MEMBERS_REF_PATH);
      await push(membersRef, memberData);
      return { success: true };
    } catch (error) {
      setError(error.message);
      return { success: false, error: error.message };
    }
  };

  /**
   * Sger hej d till en teammedlem 
   *
   * Som att sudda ut ngon frn gstboken - tar bort personen
   * helt frn teamet. Inga spr kvar!
   *
   * @param {string} memberId - Vem som ska lmna teamet
   * @returns {Object} Berttar om det gick bra eller inte
   */
  const deleteMember = async (memberId) => {
    try {
      const memberRef = ref(db, `${MEMBERS_REF_PATH}/${memberId}`);
      await remove(memberRef);
      return { success: true };
    } catch (error) {
      setError(error.message);
      return { success: false, error: error.message };
    }
  };

  // Packar ihop allt i en snygg lda fr andra komponenter att anvnda
  return {
    members, // Alla fantastiska mnniskor i teamet
    loading, // Hller vi p att hmta data?
    error, // Har ngot gtt fel?
    addMember, // Vlkomna nya medlemmar
    deleteMember, // Sga hej d till medlemmar
  };
};

export default useMembers;
