// src/components/Tasks/TaskCard.jsx
import React, { useState, memo, useMemo, useCallback } from "react";
import Button from "../UI/Button.jsx";
import Modal from "../UI/Modal.jsx";
import { formatDate, formatRelativeTime } from "../../utils/dateUtils.jsx";
import "../../styles/components/TaskCard.css";

/**
 * Uppgiftskort - visuell representation av en uppgift
 *
 * Visar en uppgift som ett interaktivt kort med all relevant information.
 * Fungerar som en digital post-it med mjlighet att:
 * - Visa uppgiftens status, kategori och tilldelning
 * - ppna detaljvy med fullstndig information
 * - ndra status (flytta mellan kolumner)
 * - Tilldela uppgiften till rtt teammedlem
 * - Ta bort uppgiften frn systemet
 *
 * @param {Object} task - Uppgiftsdata att visa
 * @param {Array} members - Tillgngliga teammedlemmar
 * @param {Function} onUpdateStatus - Callback fr statusndring
 * @param {Function} onAssignTask - Callback fr tilldelning
 * @param {Function} onDeleteTask - Callback fr borttagning
 */
const TaskCard = ({
  task,
  members,
  onUpdateStatus,
  onAssignTask,
  onDeleteTask,
}) => {
  // States fr modal och tilldelning
  const [showingTaskDetails, setShowingTaskDetails] = useState(false);
  const [askingForDeleteConfirmation, setAskingForDeleteConfirmation] =
    useState(false);
  const [chosenTeammateId, setChosenTeammateId] = useState("");

  /**
   * Hittar personer med rtt kompetens
   *
   * OPTIMERING: Memoizerad fr bttre prestanda
   */
  const qualifiedTeammates = useMemo(
    () => members.filter((teammate) => teammate.role === task.category),
    [members, task.category]
  );

  /**
   * Min frgkodning fr var uppgiften befinner sig
   *
   * Som trafikljus fr arbetsfldet - bltt fr "vnta",
   * orange fr "kr", grnt fr "klart". Enkelt att frst!
   */
  const getWorkflowColor = (status) => {
    switch (status.toLowerCase()) {
      case "nytt":
        return "status-new";
      case "pgende":
        return "status-in-progress";
      case "klar":
        return "status-completed";
      default:
        return "";
    }
  };

  /**
   * Min frgkodning fr olika typer av arbete
   *
   * Varje kompetensomrde fr sin egen frg s man snabbt
   * ser vad som r vad p tavlan. Som att sortera LEGO!
   */
  const getSkillAreaColor = (category) => {
    switch (category.toLowerCase()) {
      case "frontend":
        return "category-frontend";
      case "backend":
        return "category-backend";
      case "ux":
        return "category-ux";
      default:
        return "category-default";
    }
  };

  /**
   * Flyttar uppgiften framt i arbetsfldet
   *
   * Som att trycka p "nsta"-knappen i en process.
   * Stnger ocks detaljvyn s man ser resultatet direkt.
   *
   * OPTIMERING: Memoizerad callback fr att undvika ondiga re-renders
   */
  const moveTaskForward = useCallback(
    (newStatus) => {
      onUpdateStatus(task.id, newStatus);
      setShowingTaskDetails(false);
    },
    [onUpdateStatus, task.id]
  );

  /**
   * Ger jobbet till rtt person
   *
   * Hittar den valda personen och sger "det hr r ditt nu!".
   * Stnger sedan detaljvyn s man ser frndringen.
   *
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   */
  const giveTaskToChosen = useCallback(() => {
    if (!chosenTeammateId) return;

    const chosenPerson = members.find((m) => m.id === chosenTeammateId);
    if (chosenPerson) {
      onAssignTask(task.id, chosenPerson.id, chosenPerson.name);
      setShowingTaskDetails(false);
    }
  }, [chosenTeammateId, members, onAssignTask, task.id]);

  /**
   * Raderar uppgiften fr alltid
   *
   * Som att kasta ngot i papperskorgen och tmma den.
   * Stnger alla ppna fnster eftert.
   *
   * OPTIMERING: Memoizerad callback fr bttre prestanda
   */
  const eraseTaskForever = useCallback(() => {
    onDeleteTask(task.id);
    setAskingForDeleteConfirmation(false);
    setShowingTaskDetails(false);
  }, [onDeleteTask, task.id]);

  /**
   * Renderar uppgiftskortet med alla dess komponenter
   *
   * Bestr av tre huvuddelar:
   * - Kompakt kort fr visning p tavlan
   * - Detaljmodal med fullstndig information
   * - Bekrftelsemodal fr borttagning
   */
  return (
    <>
      {/* Det lilla kortet p tavlan - klicka fr att se mer! */}
      <div className="task-card" onClick={() => setShowingTaskDetails(true)}>
        {/* Status och typ visas hgst upp med frgkodning */}
        <div className="task-header">
          <span className={`task-status ${getWorkflowColor(task.status)}`}>
            {task.status}
          </span>
          <span className={`task-category ${getSkillAreaColor(task.category)}`}>
            {task.category}
          </span>
        </div>

        {/* Vad uppgiften handlar om - i stora bokstver */}
        <h3 className="task-title">{task.title}</h3>

        {/* Kort beskrivning - kapar texten om den r fr lng */}
        {task.description && (
          <p className="task-description">
            {task.description.length > 100
              ? `${task.description.substring(0, 100)}...`
              : task.description}
          </p>
        )}

        {/* Vem som gr uppgiften och nr den skapades */}
        <div className="task-meta">
          {/* Vem jobbar med detta? Eller r det ledigt? */}
          {task.assignedToMemberName ? (
            <div className="task-assigned">
              <span className="meta-label">Tilldelad:</span>
              <span className="meta-value">{task.assignedToMemberName}</span>
            </div>
          ) : (
            <div className="task-assigned">
              <span className="meta-label">Tilldelad:</span>
              <span className="meta-value not-assigned">Ej tilldelad</span>
            </div>
          )}

          {/* Nr skapades uppgiften? Visar "2 dagar sedan" istllet fr exakt datum */}
          <div className="task-created">
            <span className="meta-label">Skapad:</span>
            <span
              className="meta-value"
              title={formatDate(task.creationTimestamp)}
            >
              {formatRelativeTime(task.creationTimestamp)}
            </span>
          </div>
        </div>
      </div>

      {/* Stor popup med alla detaljer och knappar */}
      <Modal
        isOpen={showingTaskDetails}
        onClose={() => setShowingTaskDetails(false)}
        title={task.title}
        size="medium"
        footer={
          <>
            {/* Ta bort-knappen (ppnar en "r du sker?"-ruta) */}
            <Button
              onClick={() => setAskingForDeleteConfirmation(true)}
              variant="danger"
            >
              Ta bort
            </Button>

            {/* Tomt utrymme som trycker isr knapparna */}
            <div style={{ flex: 1 }}></div>

            {/* "Klar"-knappen visas bara om uppgiften inte redan r klar */}
            {task.status !== "Klar" && (
              <Button onClick={() => moveTaskForward("Klar")} variant="success">
                Markera som klar
              </Button>
            )}

            {/* "Starta"-knappen visas bara fr nya uppgifter */}
            {task.status === "Nytt" && (
              <Button
                onClick={() => moveTaskForward("Pgende")}
                variant="primary"
              >
                Starta uppgift
              </Button>
            )}
          </>
        }
      >
        <div className="task-details">
          {/* Var r uppgiften i fldet? */}
          <div className="task-detail-row">
            <span className="detail-label">Status:</span>
            <span
              className={`detail-value task-status ${getWorkflowColor(
                task.status
              )}`}
            >
              {task.status}
            </span>
          </div>

          {/* Vilken typ av uppgift? */}
          <div className="task-detail-row">
            <span className="detail-label">Kategori:</span>
            <span
              className={`detail-value task-category ${getSkillAreaColor(
                task.category
              )}`}
            >
              {task.category}
            </span>
          </div>

          {/* Nr skapades uppgiften? */}
          <div className="task-detail-row">
            <span className="detail-label">Skapad:</span>
            <span className="detail-value">
              {formatDate(task.creationTimestamp)}
            </span>
          </div>

          {/* Detaljerad beskrivning av vad som ska gras */}
          {task.description && (
            <div className="task-detail-description">
              <h4>Beskrivning</h4>
              <p>{task.description}</p>
            </div>
          )}

          {/* Vem ska gra jobbet? */}
          <div className="task-assignment">
            <h4>Tilldelning</h4>

            {/* Redan tilldelad? Visa vem och ge mjlighet att ndra */}
            {task.assignedToMemberName ? (
              <div className="current-assignment">
                <p>
                  Denna uppgift r tilldelad till:{" "}
                  <strong>{task.assignedToMemberName}</strong>
                </p>
                <Button
                  onClick={() => onAssignTask(task.id, null, null)}
                  variant="secondary"
                  size="small"
                >
                  Ta bort tilldelning
                </Button>
              </div>
            ) : (
              /* Inte tilldelad? Visa formulr fr att vlja person */
              <div className="assignment-form">
                {/* Lista med personer som har rtt kompetens fr jobbet */}
                {qualifiedTeammates.length > 0 ? (
                  <>
                    <p>
                      Tilldela denna {task.category}-uppgift till en teammedlem
                      med matchande roll:
                    </p>
                    <div className="assignment-controls">
                      <select
                        value={chosenTeammateId}
                        onChange={(e) => setChosenTeammateId(e.target.value)}
                      >
                        <option value="">Vlj teammedlem</option>
                        {qualifiedTeammates.map((teammate) => (
                          <option key={teammate.id} value={teammate.id}>
                            {teammate.name}
                          </option>
                        ))}
                      </select>
                      <Button
                        onClick={giveTaskToChosen}
                        variant="primary"
                        disabled={!chosenTeammateId}
                      >
                        Tilldela
                      </Button>
                    </div>
                  </>
                ) : (
                  /* Inga personer med rtt kompetens? Visa ett meddelande */
                  <p className="no-eligible-members">
                    Det finns inga teammedlemmar med rollen {task.category} som
                    kan tilldelas denna uppgift.
                  </p>
                )}
              </div>
            )}
          </div>
        </div>
      </Modal>

      {/* "r du sker?"-rutan fr borttagning */}
      <Modal
        isOpen={askingForDeleteConfirmation}
        onClose={() => setAskingForDeleteConfirmation(false)}
        title="Bekrfta borttagning"
        footer={
          <>
            <Button
              onClick={() => setAskingForDeleteConfirmation(false)}
              variant="secondary"
            >
              Avbryt
            </Button>
            <Button onClick={eraseTaskForever} variant="danger">
              Ta bort
            </Button>
          </>
        }
      >
        <p>
          r du sker p att du vill ta bort uppgiften "{task.title}"? Uppgiften
          kommer att tas bort permanent frn databasen och kan inte terstllas.
        </p>
      </Modal>
    </>
  );
};

// OPTIMERING: Memoizerad komponent fr att undvika ondiga re-renders
// Komponenten renderas bara om nr props faktiskt ndras
export default memo(TaskCard);
