// src/components/UI/Notification.jsx
/**
 * Popup-meddelanden som frsvinner automatiskt
 *
 * Fyra typer: info, success, warning, error.
 */
import React, { useState, useEffect } from "react";
import "../../styles/components/Notification.css";

const Notification = ({ message, type = "info", duration = 5000, onClose }) => {
  const [isVisible, setIsVisible] = useState(true);

  useEffect(() => {
    // Inget meddelande? Gr inget
    if (!message) return;

    // Visa meddelandet
    setIsVisible(true);

    // Frsvinner automatiskt
    const timer = setTimeout(() => {
      setIsVisible(false);
      // Vnta p animation innan borttagning
      if (onClose) setTimeout(onClose, 300);
    }, duration);

    // Stda upp
    return () => clearTimeout(timer);
  }, [message, duration, onClose]);

  if (!message) return null;

  // Nr du klickar p krysset
  const handleClose = () => {
    setIsVisible(false); // Starta frsvinn-animationen
    // Vnta p animationen innan vi tar bort helt
    if (onClose) setTimeout(onClose, 300);
  };

  return (
    <div
      className={`notification notification-${type} ${
        isVisible ? "show" : "hide"
      }`}
    >
      <div className="notification-content">
        {/* Ikoner fr olika typer */}
        {type === "success" && <span className="notification-icon"></span>}
        {type === "error" && <span className="notification-icon"></span>}
        {type === "warning" && <span className="notification-icon"></span>}
        {type === "info" && <span className="notification-icon"></span>}
        {/* Meddelandet */}
        <p className="notification-message">{message}</p>
      </div>
      {/* Stng-knapp */}
      <button
        className="notification-close"
        onClick={handleClose}
        aria-label="Stng"
      >
        
      </button>
    </div>
  );
};

export default Notification;
