// src/utils/dateUtils.jsx
// Anvnder en enklare approach fr att undvika Parcel-problem med date-fns

/**
 * Tidsfunktioner fr anvndarvnliga datum
 *
 * Gr tidsstmplar begripliga med snygga format
 * och relativa texter som "2 timmar sedan".
 */

/**
 * Formaterar tidsstmpel till lsbart datum
 *
 * @param {number} timestamp - Tidsstmpel
 * @returns {string} Format: "2025-05-15 14:30"
 */
export const formatDate = (timestamp) => {
  // Ingen tidsstmpel? Returnera tom strng
  if (!timestamp) return "";

  // Skapa Date-objekt och formatera
  const date = new Date(timestamp);

  // Formatera med svenska format
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, "0");
  const day = String(date.getDate()).padStart(2, "0");
  const hours = String(date.getHours()).padStart(2, "0");
  const minutes = String(date.getMinutes()).padStart(2, "0");

  return `${year}-${month}-${day} ${hours}:${minutes}`;
};

/**
 * Visar relativ tid sedan hndelse
 *
 * @param {number} timestamp - Tidsstmpel
 * @returns {string} Text som "2 dagar sedan" eller "Just nu"
 */
export const formatRelativeTime = (timestamp) => {
  // Ingen tidsstmpel? Returnera tom strng
  if (!timestamp) return "";

  // Berkna tidsskillnad
  const now = Date.now();
  const diff = now - timestamp;

  // Konvertera till lsbara enheter
  const minutes = Math.floor(diff / 60000);
  const hours = Math.floor(minutes / 60);
  const days = Math.floor(hours / 24);

  // Vlj lmplig text
  if (days > 0) {
    // Singular/plural fr dagar
    return `${days} ${days === 1 ? "dag" : "dagar"} sedan`;
  } else if (hours > 0) {
    // Singular/plural fr timmar
    return `${hours} ${hours === 1 ? "timme" : "timmar"} sedan`;
  } else if (minutes > 0) {
    // Singular/plural fr minuter
    return `${minutes} ${minutes === 1 ? "minut" : "minuter"} sedan`;
  } else {
    // Mycket nyligen
    return "Just nu";
  }
};
