// src/components/Admin/DatabaseSeeder.jsx
import React, { useState, useEffect } from "react";
import {
  seedDatabase,
  testDatabaseConnection,
  clearDatabase,
  checkIfExampleDataExists,
} from "../../firebase/seedDatabase";
import { useAuth } from "../../hooks/useAuth.jsx";
import {
  DatabaseIcon,
  ChevronDownIcon,
  ChevronRightIcon,
  LoadingIcon,
  CheckIcon,
  AlertIcon,
  SearchIcon,
  DatabasePlusIcon,
  TrashIcon,
} from "../UI/Icons";
import "../../styles/components/Button.css";

/**
 * Databasverktyg - hantering av testdata
 *
 * Kompakt verktyg fr att hantera testdata i utvecklingsmiljn.
 * Visar databasanslutningsstatus och lter anvndaren fylla eller
 * rensa databasen med ett klick.
 *
 * @param {Function} onSuccess - Callback fr statusmeddelanden
 */
const DatabaseSeeder = ({ onSuccess }) => {
  const [loading, setLoading] = useState(false);
  const [connectionStatus, setConnectionStatus] = useState("checking");
  const [isExpanded, setIsExpanded] = useState(false);
  const [hasExampleData, setHasExampleData] = useState(false);

  // Hmta autentiseringsstatus fr att kontrollera om anvndaren r inloggad
  const { currentUser } = useAuth();

  // Kollar om databasen fungerar och om exempeldata finns direkt nr sidan laddas
  useEffect(() => {
    // Bara kr databaskontrollen om anvndaren r inloggad
    if (!currentUser) {
      setConnectionStatus("error");
      return;
    }

    // Frhindra flera samtidiga anrop genom att anvnda en flagga
    let isCheckingConnection = false;

    const checkConnection = async () => {
      // Undvik flera samtidiga anrop
      if (isCheckingConnection) {
        console.log("Databasanslutning kontrolleras redan, hoppar ver...");
        return;
      }

      isCheckingConnection = true;

      try {
        const result = await testDatabaseConnection();
        setConnectionStatus(result.success ? "connected" : "error");

        if (!result.success) {
          onSuccess(
            `Fel vid anslutning till databasen: ${result.error}`,
            "error"
          );
        } else {
          // Kontrollera om exempeldata redan finns - bara om anvndaren r inloggad
          if (currentUser) {
            try {
              const storedFlag = localStorage.getItem("exampleDataAdded");
              if (storedFlag === "true") {
                // Dubbelkolla mot databasen fr att vara sker
                const dataExists = await checkIfExampleDataExists();
                setHasExampleData(dataExists);
              } else {
                // Om ingen flagga finns, kolla nd databasen
                const dataExists = await checkIfExampleDataExists();
                setHasExampleData(dataExists);
              }
            } catch (dataError) {
              // Om det blir fel vid kontroll av exempeldata, stt bara till false
              console.log(
                "Kunde inte kontrollera exempeldata, stter till false"
              );
              setHasExampleData(false);
            }
          } else {
            // Om anvndaren inte r inloggad, stt bara hasExampleData till false
            setHasExampleData(false);
          }
        }
      } catch (error) {
        setConnectionStatus("error");
        onSuccess(
          `Fel vid test av databasanslutning: ${error.message}`,
          "error"
        );
      } finally {
        isCheckingConnection = false;
      }
    };

    // Lgg till en liten frdrjning fr att undvika race conditions
    const timeoutId = setTimeout(checkConnection, 100);

    return () => {
      clearTimeout(timeoutId);
      isCheckingConnection = false;
    };
  }, [currentUser]); // BORTTAGET: onSuccess frn dependencies fr att undvika loopar

  /**
   * Fyller databasen med testdata
   *
   * Skapar exempeldata med teammedlemmar och uppgifter
   * fr att demonstrera applikationens funktionalitet.
   */
  const handleSeedDatabase = async () => {
    // Bekrfta tgrden
    const confirmSeed = window.confirm(
      "Lgg till testdata i databasen?\n\n" +
        "Detta kommer att fylla databasen med exempeldata fr team och uppgifter.\n\n" +
        "OBS! Befintlig data kommer att ersttas. Vill du fortstta?"
    );

    if (!confirmSeed) return;

    setLoading(true);
    try {
      // Visa statusmeddelande
      onSuccess("Skapar testdata...", "info");

      // Kr funktionen som rensar och fyller databasen
      const result = await seedDatabase();

      if (result.success) {
        // Uppdatera status
        setHasExampleData(true);

        // Visa bekrftelse
        onSuccess(
          "Klart! Databasen har fyllts med testdata fr team och uppgifter.",
          "success"
        );
      } else {
        onSuccess(`Ett fel uppstod: ${result.error}`, "error");
      }
    } catch (error) {
      console.error("Fel vid seedDatabase:", error);
      onSuccess(`Ett ovntat fel uppstod: ${error.message}`, "error");
    } finally {
      setLoading(false);
    }
  };

  /**
   * Rensar databasen
   *
   * Tar bort all data frn databasen fr att starta om
   * med en tom databas.
   */
  const handleClearDatabase = async () => {
    // Bekrfta tgrden
    const confirmClear = window.confirm(
      "Rensa all data frn databasen?\n\n" +
        "Detta kommer att ta bort alla team och uppgifter frn databasen.\n\n" +
        "Det gr inte att ngra denna tgrd. Vill du fortstta?"
    );

    if (!confirmClear) return;

    setLoading(true);
    try {
      // Visa statusmeddelande
      onSuccess("Rensar databasen...", "info");

      // Kr funktionen som rensar databasen
      const result = await clearDatabase();

      if (result.success) {
        // Uppdatera status
        setHasExampleData(false);

        // Visa bekrftelse
        onSuccess("Klart! Databasen har rensats och r nu tom.", "success");
      } else {
        onSuccess(`Ett fel uppstod: ${result.error}`, "error");
      }
    } catch (error) {
      console.error("Fel vid clearDatabase:", error);
      onSuccess(`Ett ovntat fel uppstod: ${error.message}`, "error");
    } finally {
      setLoading(false);
    }
  };

  // Vxla mellan expanderat och kompakt lge
  const toggleExpand = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <div className={`database-seeder ${isExpanded ? "expanded" : "collapsed"}`}>
      <div className="seeder-header" onClick={toggleExpand}>
        <div className="seeder-header-content">
          <span className="seeder-header-icon">
            <DatabaseIcon size={16} />
          </span>
          <h3>Databasverktyg</h3>
        </div>
        <span className="expand-icon">
          {isExpanded ? (
            <ChevronDownIcon size={16} />
          ) : (
            <ChevronRightIcon size={16} />
          )}
        </span>
      </div>

      <div className="seeder-content">
        <div className="seeder-content-inner">
          <div className="status-indicators">
            {/* Databasanslutningsstatus */}
            <div className={`connection-status ${connectionStatus}`}>
              {connectionStatus === "checking" && (
                <>
                  <span className="status-icon">
                    <LoadingIcon size={16} />
                  </span>
                  Ansluter till Firebase...
                </>
              )}
              {connectionStatus === "connected" && (
                <>
                  <span className="status-icon">
                    <CheckIcon size={16} color="#10b981" />
                  </span>
                  Ansluten till Firebase-databasen
                </>
              )}
              {connectionStatus === "error" && (
                <>
                  <span className="status-icon">
                    <AlertIcon size={16} color="#ef4444" />
                  </span>
                  Kunde inte ansluta till Firebase
                </>
              )}
            </div>

            {/* Status fr exempeldata */}
            {connectionStatus === "connected" && (
              <div
                className={`example-data-status ${
                  hasExampleData ? "exists" : "none"
                }`}
              >
                <span className="status-icon">
                  <SearchIcon size={16} />
                </span>
                {hasExampleData
                  ? "Testdata finns i databasen"
                  : "Databasen r tom"}
              </div>
            )}
          </div>

          <div className="seeder-description">
            Hantera testdata fr din Scrum Board. Fyll databasen med frdiga
            exempel p team och uppgifter fr att snabbt komma igng, eller
            rensa allt fr att brja om frn brjan.
          </div>

          <div className="seeder-buttons">
            {!hasExampleData && (
              <button
                className="button seed-button"
                onClick={handleSeedDatabase}
                disabled={loading || connectionStatus !== "connected"}
              >
                {loading ? (
                  <>
                    <LoadingIcon size={16} className="loading-spinner" />
                    Skapar testdata...
                  </>
                ) : (
                  <>
                    <DatabasePlusIcon size={16} />
                    Lgg till testdata
                  </>
                )}
              </button>
            )}

            {hasExampleData && (
              <button
                className="button clear-button"
                onClick={handleClearDatabase}
                disabled={loading || connectionStatus !== "connected"}
              >
                {loading ? (
                  <>
                    <LoadingIcon size={16} className="loading-spinner" />
                    Rensar databasen...
                  </>
                ) : (
                  <>
                    <TrashIcon size={16} />
                    Rensa databasen
                  </>
                )}
              </button>
            )}
          </div>

          {connectionStatus === "error" && (
            <div className="connection-error">
              <p>
                <strong>Anslutningsfel</strong> Kunde inte ansluta till
                Firebase-databasen.
              </p>
              <p>Kontrollera fljande:</p>
              <ul>
                <li>r Firebase-projektet korrekt konfigurerat?</li>
                <li>r databasens URL korrekt i firebaseConfig.jsx?</li>
                <li>Har du rtt behrigheter fr databasen?</li>
                <li>
                  Tillter databasens skerhetsregler lsning och skrivning?
                </li>
              </ul>
            </div>
          )}

          {connectionStatus === "connected" && (
            <div className="seeder-info">
              <p className="seeder-warning">
                <strong>Observera:</strong> Detta verktyg r endast fr
                testning. Befintlig data kommer att ersttas om du lgger till
                testdata eller rensar databasen. Anvnd med frsiktighet.
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default DatabaseSeeder;
